<script>
import Bus from "../../../utils/Bus";

export default {
  name: "AccountInfoThermal",
  props: {orgTree: Object},
  destroyed() {
    Bus.$off('globalMsgForThermalUser');
  },
  data() {
    let Caption = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    let LoginId = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (!this.isCellPhone(value)) {
        callback(new Error('!'))
      } else {
        callback()
      }
    };
    let Password = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    let Phone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (!this.isCellPhone(value)) {
        callback(new Error('!'))
      } else {
        callback()
      }
    };

    return {
      organization_name: '',
      obj_bank_center_id: [],
      obj_province_id: [],
      obj_city_id: [],
      obj_county_id: [],
      obj_station_id: [],
      tmpCaption: [],
      clmShow: false,
      ballShow: false,
      showTree: true,
      menuLeft: '-180',  //
      menuLeft2: '80',  //
      menuWidth: '80px',//    
