    /**  */
    getPlatformUserBtn(username) {
      let _jsondata_ = {
        username: username,
      };
      //  console.info("===ID======"+JSON.stringify(_jsondata_));
      //4post
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryUserOrgAndPermissionKey,// 
        this.customGlobal.serverUrl.orgBaseURI,//
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post
      p1.then((res) => {
        // console.log(":"+JSON.stringify(res));
        if (res.data.status == 'success') {
          //  map
          this.tableBtnRowIdMap = new Map();
          this.userQueryBtnIds = {};
          //ID
          let btnKey = JSON.parse(res.data.btnkey);

          this.userQueryBtnIds = btnKey;
          // console.log("===ID=====ID============="+permissionKey);
          let i = 0;
          for (let p1 of Object.keys(btnKey)) {
            if (p1 !== '-1') {
              this.tableBtnRowIdMap.set(p1, true);
              i++
            }
            // console.log("===ID=====ID============="+p1);
          }
          this.btnNum = i;
          //   
          this.noCheckBtnTreeTable(this.tableBtnTreeData)
          this.initBtnTableDataPermissionStatus(this.userQueryBtnIds, this.tableBtnTreeData);
        }
      }, error => {
        //  console.log(":"+JSON.stringify(error));
        this.$alert(JSON.stringify(error.message), '!', {
          confirmButtonText: '',
          callback: action => {
          }
        });
      }).catch((res) => {
        //  console.log(":"+JSON.stringify(res));
        this.$alert(JSON.stringify(res.message), '', {
          confirmButtonText: '',
          callback: action => {
          }
        });
      });

    },

