    /** checkbox */
    handleCheck(data, node) {
      //  console.log("check"+ JSON.stringify(data), node);
      //  console.log("check"+ JSON.stringify(data), JSON.stringify( node.checkedKeys) );
      this.checkOrgId = node.checkedKeys;
      if (node.checkedNodes.length > 0) {
        this.selType = node.checkedNodes[node.checkedNodes.length - 1].type;
        // console.log("====checkBox================="+this.selType)
      }
      this.checkAllOrgIds = [];
      this.recursion(this.treeData, false)
      // this.treeCheckedKeys = node.checkedKeys;
      this.checkAllOrgIds = this.checkAllOrgIds.reverse();

      //  console.log("====checkBox====ID============="+node.checkedKeys)
    },

    /** ID */
    recursion(arr, isChild) {
      let aCheck = [];
      for (let i = 0; i < arr.length; i++) {
        let obj = arr[i];
        aCheck[i] = false;
        if (obj.detail) {
          aCheck[i] = this.recursion(obj.detail, true) ? true : aCheck[i];
          if (aCheck[i]) {
            this.checkAllOrgIds.push(obj['id']);
          }
        }
        for (let j = 0; j < this.checkOrgId.length; j++) {
          if (obj['id'] == this.checkOrgId[j]) {
            aCheck[i] = true;
            if (this.checkAllOrgIds.indexOf(obj['id']) == -1) {
              this.checkAllOrgIds.push(obj['id']);
            }
            break;
          }
        }
      }
      if (isChild) {
        return aCheck.indexOf(true) != -1;
      }
    },

    /** tree */
    noCheckOrgTree(rows) {
      this.$refs.thermalOrgTree.setCheckedKeys(rows)
    },

    /**  */
    removeTreeNode(node, data) {
      const parent = node.parent
      const children = parent.data.detail || parent.data
      const index = children.findIndex(d => d.id === data.id)
      children.splice(index, 1)
    },

    /**  */
    initTreeDataStatus(h, {node, data, store}) {
      if (JSON.stringify(this.treeDataStatus) === "{}") {
        return (
          <span style="font-size:14px;">
               <span>{data.caption}</span>
            </span>
        )
      } else {
        let find = false;
        for (let k of Object.keys(this.treeDataStatus)) {
          if (data.id === k) {
            return (
              <span style="font-size:14px;">
                   <span>{data.caption}</span>
                </span>
            )
            find = true;
            break;
          }
        }
        if (find === false) {
          this.removeTreeNode(node, data);
        }
      }

    },

    /** 1  */
    initAccOrgTreeData(data, data2) {
      // 
      if (this.customGlobal.userInfoExt.cui_type === '0' && this.customGlobal.userInfoExt.cui_org_type === '0') {
        this.selectAdminOrgIds(data);
        return;
      }
      for (let k of Object.keys(data2)) {
        this.selectOrgIds(data, k);
      }
    },

    /** 2  */
    selectOrgIds(row, id) {
      for (let i = 0; i < row.length; i++) {
        if (row[i].id === id) {
          if (row[i].type >= this.customGlobal.userInfoExt.cui_type) {
            switch (row[i].type) {
              case 1:
                this.companyIds.push(row[i].id);
                break
              case 2:
                this.childCompanyIds.push(row[i].id);
                break
              case 3:
                this.instituteIds.push(row[i].id);
                break
              case 4:
                this.stationIds.push(row[i].id);
                break
            }
          }
        }
        if (row[i].detail) {
          this.selectOrgIds(row[i].detail, id);
        }
      }

    },

    /**  ID  */
    selectAdminOrgIds(row) {
      for (let i = 0; i < row.length; i++) {
        switch (row[i].type) {
          case 1:
            this.companyIds.push(row[i].id);
            break
          case 2:
            this.childCompanyIds.push(row[i].id);
            break
          case 3:
            this.instituteIds.push(row[i].id);
            break
          case 4:
            this.stationIds.push(row[i].id);
            break
        }
        // this.userOrgAngType.push(id);
        if (row[i].detail) {
          this.selectAdminOrgIds(row[i].detail);
        }
      }

    },

    /**  */
    initOrgTemplateInfo() {
      const loading = this.$loading({
        lock: true,
        text: '...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryOrganTemplate,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({
          jsondata: JSON.stringify({
            config: 'all',
            username: this.customGlobal.userInfoExt.cui_login_id
          })
        })
      );

      p1.then((res) => {
        //  console.log(":"+JSON.stringify(res));
        if (res.data.status == 'success') {
          this.treeData = JSON.parse(res.data.organ).detail;
          this.treeDataStatus = JSON.parse(res.data.orgkey);
          loading.close();

        } else {
          //   console.info("");
          this.treeData = [JSON.parse(res.data.organ)];
          this.treeDataStatus = JSON.parse(res.data.orgkey);
          loading.close();


        }

      }, error => {
        //  console.log(":"+JSON.stringify(error));
        loading.close();
        this.$alert(JSON.stringify(error.message), '!', {
          confirmButtonText: '',
          callback: action => {

          }
        });
      }).catch((res) => {
        //  console.log(":"+JSON.stringify(res));
        loading.close();

        this.$alert(JSON.stringify(res.message), '', {
          confirmButtonText: '',
          callback: action => {

          }
        });
      });

    },

    /**  */
    submitUserOrg() {
      if (this.checkAllOrgIds.length == 0) {
        this.$alert("", '', {
          confirmButtonText: '',
          callback: action => {
          }
        });
        return;
      }

      if (this.selType < this.customGlobal.userInfoExt.cui_org_type) {
        this.$alert("", '', {
          confirmButtonText: '',
          callback: action => {
          }
        });
        return;
      }
      let objOrgId = {};
      for (let i = 0; i < this.checkAllOrgIds.length; i++) {
        objOrgId[this.checkAllOrgIds[i]] = true
      }
      //
      this.saveUserOrgKeys(this.loginIdByOrg, objOrgId);
      //
      this.$nextTick(() => {
        this.$refs.thermalOrgTree.setCheckedKeys(this.checkAllOrgIds);
      });
      this.userOrgId = [];
      this.platformUserOrgVisible = false;
      //  console.log("======================"+this.userOrgId);
    },

    /**  */
    saveUserOrgKeys(loginId, objOrgId) {
      let _jsondata_ = {
        username: loginId,
        orgkey: objOrgId
      };
      const loading = this.$loading({
        lock: true,
        text: '...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, .7)'
      });
      //  console.info("==========="+JSON.stringify(_jsondata_));
      //4post
      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postCreateUserAndOrgKey,// 
        this.customGlobal.serverUrl.orgBaseURI,//
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      //post
      p1.then((res) => {
        // console.log(":"+JSON.stringify(res));
        this.thermalUserOrgVisible = false;
        loading.close();
        if (res.data.status == 'success') {
          this.$message({
            type: 'success',
            message: '!'
          });
        } else {
          this.$alert("" + JSON.stringify(error.message), '!', {
            confirmButtonText: '',
            callback: action => {
            }
          });
        }
      }, error => {
        loading.close();
        //  console.log(":"+JSON.stringify(error));
        this.$alert(JSON.stringify(error.message), '!', {
          confirmButtonText: '',
          callback: action => {
          }
        });
      }).catch((res) => {
        loading.close();
        //  console.log(":"+JSON.stringify(res));
        this.$alert(JSON.stringify(res.message), '', {
          confirmButtonText: '',
          callback: action => {
          }
        });
      });
    },

  },


}
</script>

